.nodata{max-width:780px;width:100%;padding:20px;margin:0 auto;display:-webkit-box;display:-moz-flex;display:-ms-flexbox;display:flex;-webkit-box-pack:end;-ms-flex-pack:end;-moz-justify-content:flex-end;justify-content:flex-end;-webkit-box-align:center;-ms-flex-align:center;-moz-align-items:center;align-items:center;-webkit-box-direction:normal;-webkit-box-orient:vertical;-moz-flex-direction:column;-ms-flex-direction:column;flex-direction:column;height:312px;background:rgba(0,0,0,0.4);border-radius:5px;margin-bottom:30px;margin-top:30px;position:relative}.nodata .logo-left-box{position:absolute;left:25px;top:25px;color:#fff}.nodata .logo-left-box .logo{margin-bottom:5px}.nodata .logo-left-box .notice{margin-top:5px;margin-bottom:10px;position:relative}.nodata .logo-left-box .notice::after{content:'';height:3px;width:30px;position:absolute;background:#38b7ea;left:0;bottom:-6px}.nodata .logo-left-box .blue{color:#38b7ea}.nodata .logo-left-box .white{color:#fff}.nodata img{width:200px;height:200px}.nodata::after{content:'';position:absolute;right:0;bottom:0;width:10px;height:70px;background:#38b7ea;border-bottom-right-radius:5px}



